import React from 'react'
import {Link} from 'react-router-dom'
import './plan.css'


export default function Plan() {
    return (
        <section className="pricing py-5">
            <div className="container">
                <div className="row">
                    <div className="mb-5 fill-width"><h3 className="text-center text-white ">Select your prefered plan</h3></div>
                </div>
                <div className="row">
                <div className="col-lg-4">

                    <div className="card mb-5 mb-lg-0">
                    <div className="card-body">
                        <h5 className="card-title text-muted text-uppercase text-center">Free</h5>
                        <h6 className="card-price text-center">₦0<span className="period">/month</span></h6>
                        <hr/>
                        <ul className="fa-ul">
                            <li><span className="fa-li"><i className="fa fa-check"></i></span>Single User</li>
                            <li><span className="fa-li"><i className="fa fa-check"></i></span>5 Channels</li>
                            <li><span className="fa-li"><i className="fa fa-check"></i></span>0 Megabite</li>
                            <li className="text-muted"><span className="fa-li"><i className="fa fa-times"></i></span>Bonus Channels</li>
                            <li className="text-muted"><span className="fa-li"><i className="fa fa-times"></i></span>No Ads</li>
                            <li className="text-muted"><span className="fa-li"><i className="fa fa-times"></i></span>Dedicated Technical Support</li>
                            <li className="text-muted"><span className="fa-li"><i className="fa fa-times"></i></span>Monthly Usage Reports</li>
                        </ul>
                        <Link to={'/signup'} className="btn btn-block btn-primary text-uppercase">Subscribe</Link>
                    </div>
                    </div>
                </div>
                
                <div className="col-lg-4">
                    <div className="card mb-5 mb-lg-0">
                    <div className="card-body">
                        <h5 className="card-title text-muted text-uppercase text-center">Plus</h5>
                        <h6 className="card-price text-center">₦1,500<span className="period">/month</span></h6>
                        <hr/>
                        <ul className="fa-ul">
                            <li><span className="fa-li"><i className="fa fa-check"></i></span><strong>5 Users</strong></li>
                            <li><span className="fa-li"><i className="fa fa-check"></i></span>50 Channels</li>
                            <li><span className="fa-li"><i className="fa fa-check"></i></span>500 Megabite</li>
                            <li><span className="fa-li"><i className="fa fa-check"></i></span>Bonus Channels</li>
                            <li><span className="fa-li"><i className="fa fa-check"></i></span>No Ads</li>
                            <li className="text-muted"><span className="fa-li"><i className="fa fa-times"></i></span>Dedicated Technical Support</li>
                            <li className="text-muted"><span className="fa-li"><i className="fa fa-times"></i></span>Monthly Usage Reports</li>
                        </ul>
                        <Link to={'/signup'} className="btn btn-block btn-primary text-uppercase">Subscribe</Link>
                    </div>
                    </div>
                </div>
                
                <div className="col-lg-4">
                    <div className="card">
                    <div className="card-body">
                        <h5 className="card-title text-muted text-uppercase text-center">Pro</h5>
                        <h6 className="card-price text-center">₦4,500<span className="period">/month</span></h6>
                        <hr/>
                        <ul className="fa-ul">
                            <li><span className="fa-li"><i className="fa fa-check"></i></span><strong>15 Users</strong></li>
                            <li><span className="fa-li"><i className="fa fa-check"></i></span>150 Channels</li>
                            <li><span className="fa-li"><i className="fa fa-check"></i></span>1500 Megabite</li>
                            <li><span className="fa-li"><i className="fa fa-check"></i></span>Bonus Channels</li>
                            <li><span className="fa-li"><i className="fa fa-check"></i></span>No Ads</li>
                            <li><span className="fa-li"><i className="fa fa-check"></i></span>Dedicated Technical Support</li>
                            <li><span className="fa-li"><i className="fa fa-check"></i></span>Monthly Usage Reports</li>
                            
                        </ul>
                        <Link to={'/signup'} className="btn btn-block btn-primary text-uppercase">Subscribe</Link>
                    </div>
                    </div>
                </div>
                </div>
            </div>
            </section>
    )
}