import React from 'react'
import slider1  from '../../resource/img/sliders/slider1.jpg'
import slider2  from '../../resource/img/sliders/slider2.jpg'
import slider3  from '../../resource/img/sliders/slider3.jpg'
import './Slider.css'

export default function Slider() {
    return (
        <header>
            <div id="carouselExampleIndicators" className="carousel slide mt-100" data-ride="carousel">
                <ol className="carousel-indicators">
                <li data-target="#carouselExampleIndicators" data-slide-to="0" className="active"></li>
                <li data-target="#carouselExampleIndicators" data-slide-to="1"></li>
                <li data-target="#carouselExampleIndicators" data-slide-to="2"></li>
                </ol>
                <div className="carousel-inner" role="listbox">
                <div className="carousel-item active" style={{ backgroundImage: `url(${slider1})`}}>
                    <div className="carousel-caption d-none d-md-block">
                    <h2 className="display-4">
                        {/* <button className="btn btn-outline-dark">Sign-Up</button> */}
                    </h2>
                    
                    </div>
                </div>
                <div className="carousel-item" style={{ backgroundImage: `url(${slider2})`}}>
                    <div className="carousel-caption d-none d-md-block">
                     <h2 className="display-4">
                        {/* <button className="btn btn-outline-dark">Sign-Up</button> */}
                    </h2>
                    
                    </div>
                </div>
                <div className="carousel-item" style={{ backgroundImage: `url(${slider3})`}}>
                    <div className="carousel-caption d-none d-md-block">
                     <h2 className="display-4">
                        {/* <button className="btn btn-outline-dark">Sign-Up</button> */}
                    </h2>
                    
                    </div>
                </div>
                </div>
                <a className="carousel-control-prev" href="#carouselExampleIndicators" role="button" data-slide="prev">
                    <span className="carousel-control-prev-icon" aria-hidden="true"></span>
                    <span className="sr-only">Previous</span>
                    </a>
                <a className="carousel-control-next" href="#carouselExampleIndicators" role="button" data-slide="next">
                    <span className="carousel-control-next-icon" aria-hidden="true"></span>
                    <span className="sr-only">Next</span>
                    </a>
            </div>
            </header>

    )
}