import React from 'react'
import aladin  from '../../resource/img/posters/aladin.jpg'
import captain_marvel  from '../../resource/img/posters/captain_marvel.jpg'
import io  from '../../resource/img/posters/io.jpg'
import the_martian  from '../../resource/img/posters/the_martian.jpg'
import whd  from '../../resource/img/posters/whd.jpg'
import './trend.css'

export default function Trend() {
    return (
        <div className='trend-pad'>
            <div className="row">
                <div className="col-md-3">
                    <div className="col pb-5">
                        <h3 className="text-grey pt-5 mt-3">
                            THIS WEEKEND
                        </h3>
                        <small>Get so much for less...</small>
                        <br />
                    </div>
                </div>
                <div className="col-md-9 col-sm-12">
                    <div className="row">
                        <div className="col-sm-2 col-6 col-">
                            <div className=""><img src={aladin} alt="aladin" className='img-fit hover-shadow smooth' /></div>
                            <p className="text-gray">Aladin (2019)</p>
                        </div>
                        <div className="col-sm-2 col-6 col-">
                            <div className=""><img src={captain_marvel} alt="captain_marvel" className='img-fit hover-shadow smooth' /></div>
                            <p className="text-gray">Captain Marvel (2019)</p>
                        </div>
                        <div className="col-sm-2 col-6 col-">
                            <div className=""><img src={io} alt="io" className='img-fit hover-shadow smooth' /></div>
                            <p className="text-gray">IO (2019)</p>
                        </div>
                        <div className="col-sm-2 col-6 col-">
                            <div className=""><img src={the_martian} alt="the_martian" className='img-fit hover-shadow smooth' /></div>
                            <p className="text-gray">The Martian (2015)</p>
                        </div>
                        <div className="col-sm-2 col-6 col-">
                            <div className=""><img src={whd} alt="whd" className='img-fit hover-shadow smooth' /></div>
                            <p className="text-gray">White House Down (2013)</p>
                        </div>
                    </div>
                </div>
            </div>
        </div>

    )
}
